@import url(https://fonts.googleapis.com/css2?family=Ropa+Sans&display=swap);
body{font-family:"Ropa Sans", sans-serif;background:#fff;margin:0;padding:0;font-size:16px}html,body,#root{overflow:hidden;height:100%;width:100%;position:fixed}*{box-sizing:border-box}.btn{background:#000;color:#fff;border-radius:0px;-webkit-appearance:none;-moz-appearance:none;appearance:none;text-align:center;padding:0.7rem;display:block;width:100%;border:0px;font-family:"Ropa Sans", sans-serif;font-size:20px;text-decoration:none}.btn.bottom{margin-top:2rem}.btn img{margin-right:0.5rem}.flex{display:flex}#Layout{position:fixed;height:100%;width:100%}#Layout #Header{color:#000;height:80px;position:fixed;width:100%;display:flex;padding-left:2rem;align-items:center;justify-content:space-between}#Layout #MenuButton{height:80px;width:80px;display:flex;align-items:center;justify-content:center;transition:all 0.2s ease}#Layout #MenuButton:active{transition:all 0.2s ease;transform:scale(0.8)}#Layout #Menu{font-size:20px;left:0;position:fixed;height:100%;width:100%;background:#000;color:#fff;z-index:99;display:flex;align-items:center;padding-left:100px}#Layout #Menu ul{margin:0;padding:0}#Layout #Menu ul li{margin-bottom:1rem;list-style:none}#Layout #Menu ul li a{text-decoration:none;color:#fff}#Layout #Menu ul li a:active{opacity:0.5}#Layout #Menu .close{position:absolute;top:30px;right:30px}#Layout #Content{width:100%;height:calc(100% - 80px);top:80px;position:fixed;overflow-y:scroll;overflow-x:hidden;-webkit-overflow-scrolling:touch}#Layout #Content .container{min-height:100%;padding:2rem}#Layout h1{color:#000;margin:0;font-size:50px;font-weight:300}#Loader{position:fixed;height:100%;width:100%;top:0;left:0;display:flex;align-items:center;justify-content:center;background-color:rgba(0,0,0,0.5);z-index:1000}#Loader svg{height:100px;width:100px}#Settings .add{width:80px;padding:0.7rem 0rem;text-align:center;margin-left:1rem}#Settings .custom-values .custom-value{background:#d9d9d9;padding:0.7rem;font-size:20px;margin-bottom:0.5rem;position:relative}#Settings .custom-values .custom-value .remove{float:right;position:absolute;height:100%;width:40px;right:0;top:0;display:flex;justify-content:center}#Scan .customers{margin-bottom:1rem}#Scan .customers .customer{background:#d9d9d9;margin-bottom:0.5rem;font-size:20px}#Scan .customers .customer .head{padding:1rem;position:relative}#Scan .customers .customer .head .more{height:50px;width:50px;position:absolute;right:0;display:flex;justify-content:center;align-items:center;top:0}#Scan .customers .customer .head .more .less-img{transform:rotate(180deg)}#Scan .customers .content{font-size:16px;padding-top:2rem}#Scan .customers .content .remove{position:absolute;right:1rem;bottom:1rem;text-decoration:underline}#Scan .customers .content img{float:left}#Scan .customers .content .adress{margin-bottom:1.5rem}#Scan .customers .content .adress>div,#Scan .customers .content .phone>div{margin-left:1.5rem}#Scan .qr-wrapper{width:100%;height:calc(100vh - 80px);position:fixed;background:#000;left:0;top:80px;display:flex;align-items:center;z-index:99}#Scan .qr-wrapper>section{width:100%}#Scan .qr-wrapper .close{position:absolute;top:2rem;right:2rem}#Generate .generate-wrapper{position:fixed;height:calc(100vh - 80px);width:100%;background:#fff;left:0;top:80px;display:flex;align-items:center;justify-content:center;z-index:99;flex-direction:column;padding:2rem}#Generate .generate-wrapper div{margin-top:2rem;text-align:center}#Success div{margin-top:3rem}#Success div .email{text-decoration:underline}.form{padding:2rem 0rem 3rem 0rem}.form label{color:#000;display:block}.form .input-group{margin-bottom:2rem}.form input[type="tel"],.form input[type="number"],.form input[type="email"],.form input[type="text"]{font-family:"Ropa Sans", sans-serif;-webkit-appearance:none;-moz-appearance:none;appearance:none;border:0px;border-bottom:1px solid #000;font-size:20px;padding:0.5rem 0rem;width:100%;border-radius:0px}.form input[type="tel"].error,.form input[type="number"].error,.form input[type="email"].error,.form input[type="text"].error{border-bottom:1px solid red}@media (min-width: 600px){#Layout,#Header,#Content{overflow:hidden;left:50%;transform:translateX(-50%);max-width:375px;width:100%;max-height:667px}#Generate .generate-wrapper,#Scan .qr-wrapper{height:calc(667px - 80px)}}

